	module.exports = {"data":{"markdownRemark":{"html":"<h2>対象</h2>\n<ul>\n<li>\n<p>以下のリストの2つ以上の項目に当てはまる者全員に入会権を付与します。</p>\n</li>\n<li>\n<p>とにかく『踏み逃げ禁止』反対！</p>\n</li>\n<li>\n<p>とにかくこの同盟に入りたいんだYO！</p>\n</li>\n<li>\n<p>おせっかいって程でもないが、世話好き</p>\n</li>\n<li>\n<p>過去に踏み逃げ経験があるが恥と思わない</p>\n</li>\n<li>\n<p>己のサイトは踏み逃げ・素通りを禁止していない</p>\n</li>\n<li>\n<p>踏み逃げ禁止サイトを見ていると何となくむかつく</p>\n</li>\n<li>\n<p>踏み逃げ禁止サイトを見ていると「ココもまだまだ青いな」と思えてくる</p>\n</li>\n<li>\n<p>踏み逃げ禁止サイトを見ていると己の初心者時代がふと思い浮かぶ</p>\n<h2>活動内容</h2>\n</li>\n<li>\n<p>とにかく踏み逃げしまくる！</p>\n</li>\n<li>\n<p>『踏み逃げ禁止』を謳ってるサイトでは絶対に踏み逃げ！</p>\n</li>\n<li>\n<p>『踏み逃げ禁止』を謳ってるサイトそのものを避けるように即行Back。</p>\n</li>\n<li>\n<p>『踏み逃げ禁止』でないサイトは会員の気分次第で報告を認める。</p>\n</li>\n<li>\n<p>自分のサイトにバナーを貼り付けてこの同盟の存在をアピールする。</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2002-08-08T05:57:01+09:00","strDate":"2002/8/7","title":"募集要項"}}},"pathContext":{"path":"/ja/blog/2002/08/08-募集要項/","langKey":"ja"}}

